<template>
  <div class="ranking">
    <div class="ranking_left">
      <ul>
        <li :class="{actived : isActive === t }" v-for="t in typeList"  @click="isActive = t">{{t}}</li>
      </ul>
    </div>
    <div class="ranking_right" v-infinite-scroll="loadMore"
  infinite-scroll-disabled="loading"
  infinite-scroll-distance="10">
      <div class="books" v-for="(b,index) in bookList">
        <div class="bLeft">
           <img :src="b.path" class="xImg">
        </div>
        <div class="sug ef">
          <div>{{b.name}}</div>
          <div class="word lc">{{b.synopsis}}</div>
          <div class="wsp lc">
            <span>
              <i class="mintui mintui-zuozhe"></i>{{b.author}}
            </span>
            <span class="sca" style="margin-right: -6px;">
              <span>{{b.type}}</span>
              <span>25万字</span>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {Indicator} from 'mint-ui';
import { InfiniteScroll } from 'mint-ui';
export default {
  name:'ranking',
  data(){
    return {
      bookList:[],
      typeList:['风云榜','畅销榜','点击榜','推荐榜','打赏榜','更新榜','签约榜','新书榜','新人榜','红包榜','书单榜'],
      isActive:'风云榜',
      load:false
    }
  },
  mounted(){
    this.getBook();
  },
  methods:{
    loadMore(){
      this.loading = true;
      setTimeout(() => {
        this.getBook();
        this.loading = false;
      }, 1500);
    },
    getBook(){
      Indicator.open('加载中');
      this.axios.post('/vueBook/bookList',{})
      .then((res)=>{
        if(res.data.state === 200){
          Indicator.close();
          for (let [index,b] of res.data.data.entries()){
            this.bookList.push(b);
          }
        }
      })
      .catch((err)=>{
        console.log(err)
      });
    }
  }
}
</script>
<style scoped>
  .ranking {
    overflow: hidden;
    margin-top: 2.5rem;
  }
  .ranking_left {
    float: left;
    width: 4.5rem;
    background-color: #f5f5f5;
    position: fixed;
    top: 2.5rem;
    bottom: 0;
    font-family: SimSun
  }
  .ranking_left li {
    padding: 0.8rem 0;
    cursor:pointer;
  }
  .ranking_right {
    float: right;
  }
  .ranking_right .books {
    margin:0.5rem 0 0 0.5rem;
  }
  .actived {
    background: #fff;
    border-left: 2px solid red;
  }

</style>


